import React, { Component } from "react";
import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";

export default class Example extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isOpen: false,
        };

        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen,
        });
    }

    render() {
        const { data } = this.props;

        const { currentPage } = this.props;
        return (
            <div>
                <Navbar
                    color="light"
                    light
                    expand="md"
                    className="navbar fixed-top bg-white navbar-expand-lg navbar-light maison-bold text-uppercase px-4 py-2"
                >
                    <NavbarBrand href="/">
                        <img
                            className="header-logo-width"
                            src="../../images/gojek-logo.png"
                            alt="Gojek Logo"
                            title="Gojek"
                        />
                    </NavbarBrand>
                    <NavbarToggler onClick={this.toggle} />
                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="ml-auto" navbar>
                            {data.map((menuItem, key) => (
                                <NavItem
                                    key={key}
                                    className={
                                        `mx-2 header-menu nav-item ${
                                            data.type === "dropdown" &&
                                            "dropdown"
                                        }` +
                                        (currentPage === menuItem.link
                                            ? " active"
                                            : "")
                                    }
                                >
                                    {` `}
                                    <NavLink
                                        href={menuItem.link}
                                        className="mx-2 header-menu nav-item"
                                    >
                                        {menuItem.type === `dropdown` ? (
                                            <UncontrolledDropdown nav inNavbar>
                                                <DropdownToggle
                                                    nav
                                                    caret
                                                    className="py-0"
                                                >
                                                    News &amp; Articles
                                                </DropdownToggle>
                                                <DropdownMenu right>
                                                    {menuItem.options.map(
                                                        (item, i) => (
                                                            <a
                                                                key={i}
                                                                className="dropdown-item navbar-dropdown-item text-muted text-uppercase text-left"
                                                                href={item.link}
                                                            >
                                                                {item.name}
                                                            </a>
                                                        )
                                                    )}
                                                </DropdownMenu>
                                            </UncontrolledDropdown>
                                        ) : (
                                            <span>
                                                {menuItem.name}
                                                {currentPage ===
                                                menuItem.link ? (
                                                    <span className="sr-only">
                                                        (current)
                                                    </span>
                                                ) : (
                                                    ``
                                                )}
                                            </span>
                                        )}
                                    </NavLink>
                                </NavItem>
                            ))}
                        </Nav>
                    </Collapse>
                </Navbar>
            </div>
        );
    }
}
